import React, { useContext } from "react";
import "../../SignUp.css";
import { Link, useNavigate } from "react-router-dom";
import { ElearningContext } from "../../context/ElearningContext";
import axios from "axios";

const Header = () => {
  const { deleteCookie, cookie } = useContext(ElearningContext);
  const navigate = useNavigate();

  const handleLogout = async () => {
    try {
      deleteCookie();
      await axios.delete("http://localhost:3005/auth/logout");
      navigate("/LoginPage");
    } catch (error) {
      console.log(error.message);
    }
  };

  return (
    <header>
      <nav className="navbar">
        <div className="logo">
          <img src="/glogo.png" alt="Native English Logo" />
        </div>
        <ul className="nav-links">
          <li>
            <Link to="/">Home</Link>
          </li>{" "}
          {/* Updated Home link to use Link */}
          <li>
            <a href="#about">About Us</a>
          </li>
          <li>
            <a href="#services">Services</a>
          </li>
          <li>
            <a href="#courses">Courses</a>
          </li>
          <li>
            <Link to="/dashboard">Profile/Dashboard</Link>
          </li>{" "}
          {/* Link to Pdash.js */}
        </ul>
        <button className="contact-btn">Contact</button>

        {cookie?.authenticated && (
          <button onClick={handleLogout} className="contact-btn">
            Logout
          </button>
        )}
      </nav>
    </header>
  );
};

export default Header;
