import React, { useContext, useState } from "react";
import { Link, useNavigate } from "react-router-dom"; // Import useNavigate
import "./LoginPage.css"; // You will define your custom styles here
import Header from "./components/Header/Header";
import Footer from "./components/Footer/Footer";
import { toast } from "sonner";
import axios from "axios";
import { ElearningContext } from "./context/ElearningContext";

function LoginPage() {
  const { updateLoading, updateAuthenticated, updateDetails, setStorage } =
    useContext(ElearningContext);

  const navigate = useNavigate();

  const [signUpDetails, setSignUpDetails] = useState({
    email: "",
    password: "",
  });

  const handleChanges = (e) => {
    const { name, value } = e.target;

    setSignUpDetails((prev) => ({ ...prev, [name]: value }));
  };

  const handleSignUp = async (e) => {
    e.preventDefault();

    const { email, password } = signUpDetails;

    if (email && password) {
      try {
        updateLoading(true);
        const response = await axios.post(
          `http://localhost:3005/auth/login`,
          { username: email, password },
          { withCredentials: true }
        );

        const { message, user, success, isAuthenticated } = response.data;

        if (success) {
          updateAuthenticated(isAuthenticated);
          updateDetails(user);
          toast.success(message);
          setStorage();
          navigate("/dashboard", { replace: true });
        } else {
          toast.error(message);
          console.log(message);
        }

        updateLoading(false);
      } catch (error) {
        console.log(error.message);
        updateLoading(false);
      }
    } else {
      toast.error("email and password are required");
    }
  };

  return (
    <div className="login-page">
      <Header />
      <section className="login-container">
        <div className="login-left">
          <h1>Sign In</h1>
          <p>Welcome</p>
          <p>
            If don't have an account, login. You can{" "}
            <Link to="/SignUp">Register here!</Link>
          </p>
          <img
            src="/rr.png"
            alt="Login illustration"
            className="login-illustration"
          />
        </div>
        <div className="login-right">
          <h2>Sign In</h2>
          <form className="login-form" onSubmit={handleSignUp}>
            <input
              type="text"
              name="email"
              placeholder="Enter email or user name"
              value={signUpDetails.email}
              onChange={handleChanges}
            />
            <input
              type="password"
              name="password"
              placeholder="Password"
              value={signUpDetails.password}
              onChange={handleChanges}
            />
            {/* <div className="forgot-password">
              <a href="#forgot-password">Forgot password?</a>
            </div> */}
            <button type="submit" className="login-btn">
              Sign In
            </button>
          </form>
          <div className="or">
            <p>or continue with</p>
            <div className="social-login-icons">
              <i className="fab fa-facebook"></i>
              <i className="fab fa-google"></i>
              <i className="fab fa-apple"></i>
            </div>
          </div>
        </div>
      </section>
      <Footer />
    </div>
  );
}

export default LoginPage;
