import React, { useContext } from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import HomePage from "./HomePage";
import CommunicationPage from "./CommunicationPage";
import LoginPage from "./LoginPage";
import Pdash, { Dashboard } from "./pdash";
import Teacher from "./Teacher";
import Conference from "./Conference";
import Pay from "./pay";
import Resources from "./resources";
import SignUp from "./SignUp";
import "./App.css";
import DashboardLayout from "./components/Layout";
import NotFound from "./components/NotFound";
import { Toaster } from "sonner";
import { Spin } from "antd";
import ElearningProvider, {
  ElearningContext,
} from "./context/ElearningContext";
import ProtectedRoute from "./secure/ProtectedRoute";
import ProtectLoggedIn from "./secure/ProtectLoggedIn";
import { Grade } from "./Grade";

function App() {
  const { loading } = useContext(ElearningContext);

  return (
    <>
      <Toaster position="top-center" richColors />
      <Spin spinning={loading} fullscreen />

      <Router>
        <Routes>
          <Route path="/" element={<HomePage />} />

          <Route
            path="/dashboard"
            element={
              <ProtectedRoute>
                <DashboardLayout>
                  <Dashboard />
                </DashboardLayout>
              </ProtectedRoute>
            }
          />
          <Route
            path="/communicate"
            element={
              <DashboardLayout>
                <CommunicationPage />
                {/* /home/andrew/Desktop/Projects/sheila/xlearn1/src/Grade.js /home/andrew/Desktop/Projects/sheila/xlearn1/src/Grade.css */}
              </DashboardLayout>
            }
          />
          <Route
            path="/teacher"
            element={
              <DashboardLayout>
                <Teacher />
              </DashboardLayout>
            }
          />
          <Route
            path="/Conference"
            element={
              <DashboardLayout>
                <Conference />
              </DashboardLayout>
            }
          />
          <Route
            path="/pay"
            element={
              <DashboardLayout>
                <Pay />
              </DashboardLayout>
            }
          />
          <Route
            path="/pay"
            element={
              <DashboardLayout>
                <Grade />
              </DashboardLayout>
            }
          />
          <Route
            path="/resources"
            element={
              <DashboardLayout>
                <Resources />
              </DashboardLayout>
            }
          />

          <Route
            path="*"
            element={
              <DashboardLayout>
                <NotFound />
              </DashboardLayout>
            }
          />
          <Route
            path="/LoginPage"
            element={
              <ProtectLoggedIn>
                <LoginPage />
              </ProtectLoggedIn>
            }
          />
          <Route
            path="/SignUp"
            element={
              <ProtectLoggedIn>
                <SignUp />
              </ProtectLoggedIn>
            }
          />
        </Routes>
      </Router>
    </>
  );
}

export default function WrappedApp() {
  return (
    <ElearningProvider>
      <App />
    </ElearningProvider>
  );
}
